import styles from "./CardMovie.module.css"

export function CardMovie({Movie}) {
    console.log(styles);
    const UrlImage = "https://image.tmdb.org/t/p/w300" + Movie.poster_path;
    return (
    <li className={styles.cardMovie}>
        <img 
            width={230} 
            height={345}
            className={styles.cardImg} 
            src={UrlImage} 
            alt={Movie.title} 
            title={Movie.title}/>
        <div>{Movie.title}</div>
    </li>
    );
}