import { CardMovie } from "./CardMovie";
import Movies from "./Movies.json";
import styles from "./GridMovies.module.css";

export function GridMovies() {
    // console.log(Movies);
    return (
    <ul className={styles.gridMovies}>
      {Movies.map((Movie) => (
          <CardMovie key={Movie.id} Movie={Movie}/>
    ))}      
    </ul>
    );
}